.about__container{

    display: grid;
    grid-template-columns: 90% 50%;
    gap: 15;
    padding-left: 15rem;

}


.about__cards{
    display: grid; 
    grid-template-columns: repeat(3,1fr);
    gap: 1.5rem;
}


.about__card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    transition: var(--transition);
    text-align: center;
transition: var(--transition);
}

.about_card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;

}

.about_icon{
    color: var(--color-primary);
    font-size : 1.4rem ;
    margin-bottom: 1rem;
}

.about__card h5{
    font-size: 0.9rem;
}

.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
}

.about_icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;

}

.about__card h5{
    font-size: 0.95rem;

}

.about__card small{
    font-size : 0.7rem;
    color: var(--color-light);
}

.about__content p{
margin: 2rem 0 2.6rem;
color: var(--color-light);
}



@media screen and (max-width : 1024px) {

    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p{
        margin: 1rem 0 1.5rem;
    }

    }
   
    @media screen and (max-width : 600px) {
    
        .about__me{
            width: 65%;
            margin: 0rem auto 3rem;
        }
    
        .about__cards{
            gap: 1rem;
            grid-template-columns: 1fr 1fr;
        } 

        .about__content{
            text-align: center;
        }

        .about__content p{
             margin: 1.5rem 0;
        }

    }